import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, from, Observable, of } from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CommonSmhtService {
  private api = 'https://api-gateway-test.apecgroup.net/shmv2/fo'
  private systemDateSubject = new BehaviorSubject<Date | null>(null);
  public systemDate$: Observable<Date> = this.systemDateSubject.asObservable().pipe(
    switchMap((date) => {
      if (date === null) {
        return this.getDate().pipe(
          tap(fetchedDate => this.systemDateSubject.next(this.modifyDateByUTC(new Date(fetchedDate.dataResult), true)))
        );
      } else {
        return of(date);
      }
    })
  );

  private showLoadingSubject = new BehaviorSubject<boolean>(false);
  showLoadingObservable = this.showLoadingSubject.asObservable();

  private messageSocketSubject = new BehaviorSubject<any>(null);
  public messageSocket$: Observable<any> = this.messageSocketSubject.asObservable();

  private languageSubject = new BehaviorSubject<string>(localStorage.getItem('lang') || 'en');
  public language$: Observable<string> = this.languageSubject.asObservable();

  constructor(
    private httpClient: HttpClient
  ) {
  }
  setLanguage(lang: string) {
    this.languageSubject.next(lang);
  }
  showLoading(isLoading: boolean) {
    this.showLoadingSubject.next(isLoading);
  }

  getDate(): Observable<any> {
    return this.httpClient.get(this.api + '/api/sys/getWorkingDate');
  }

  updateSystemDate(): void {
    this.getDate().pipe(
      tap(fetchedDate => this.systemDateSubject.next(fetchedDate.dataResult))
    ).subscribe();
  }

  modifyDateByUTC(date: any, isGetMethod: boolean) {
    const localUTC = (new Date().getTimezoneOffset() / 60);
    const data = new Date(date);
    data.setHours(data.getHours() + (!isGetMethod ? -localUTC : localUTC));
    return data;
  }

  getMessageSocket(data: any) {
    this.messageSocketSubject.next(data);
  }
}
